import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { Entity, ViewModelContext } from '@farris/mobile-devkit';
import { BaseDataService } from './base-data.service';

export class EditDataService extends BaseDataService {

  constructor(viewModelContext: ViewModelContext) {
    super(viewModelContext);
  }

  /**
   * 
   */
  public edit(id: string): Observable<Entity> {
    this.loadingService.show();
    const edit$ = this.befRepository.editEntityById(id);
    return edit$.pipe(
      tap(
        () => {
          this.loadingService.hide();
        },
        (exception: any) => {
          this.loadingService.hide();
        }
      )
    );
  }

}
