import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { ViewModelContext, HttpMethod, HttpHeaders, HttpParams, HttpRequestConfig } from '@farris/mobile-devkit';
import { ResponseInfo } from '@farris/mobile-bef';
import { BaseDataService } from './base-data.service';


/**
 * BE
 */
class BeActionService extends BaseDataService {

  /**
   * 
   */
  constructor(protected viewModelContext: ViewModelContext) {
    super(viewModelContext);
  }

  /**
   * 
   */
  public invokeAction(
    actionUrl: string, httpMethod: HttpMethod, httpHeaders?: HttpHeaders, params?: HttpParams, body?: any
  ): Observable<ResponseInfo> {
    return this.innerInvokeAction(actionUrl, httpMethod, httpHeaders, params, body);
  }

  /**
   * 
   */
  private innerInvokeAction(
    actionUrl: string, method: HttpMethod, headers?: HttpHeaders, params?: HttpParams, body?: any
  ): Observable<any>  {
    const apiProxy = this.befRepository.apiProxy;
    const baseUrl = apiProxy.baseUrl;

    // url
    const fullActionUrl = `${baseUrl}/service/${actionUrl}`;
    const requestConfig: HttpRequestConfig = {
    };

    // headers
    if (headers ) {
      requestConfig.headers = headers;
    }

    // 
    if (params) {
      requestConfig.params = params;
    }

    // body
    if (body) {
      requestConfig.body = body;
    }

    this.loadingService.show();
    const result$ = apiProxy.request(method, fullActionUrl, requestConfig);
    return result$.pipe(
      tap(
        () => {
          this.loadingService.hide();
        },
        (error: any) => {
          this.loadingService.hide();
          console.log(error);
        }
      )
    );
  }
}


export { BeActionService };


