import { IErrorServe } from '@farris/mobile-bef';
import { NotifyService } from '../ui-services';
import { HttpUtil, HttpStatusType } from './http';
import { ExceptionLevel } from './types';
import { JsBridgeService } from '../js-bridge-service/index';
/**
 * 
 */
class ExceptionService implements IErrorServe {

  /**
   * 
   */
  constructor(private notifyService: NotifyService, private jsBridgeService: JsBridgeService) {
  }

  /**
   * 
   */
  show(error) {
    this.exception(error);
  }

  /**
 * 
 */
  private exception(error: any) {
    // session
    if (error.message && error.message.indexOf('401') !== -1) {
      this.notifyService.error('');
      setTimeout(() => { this.jsBridgeService.closeWindow() }, 2000)
      return;
    }
    if (error && error.response) {
      this.httpErrorHandler(error.response);
    }
  }

  /**
 * http
 */
  private httpErrorHandler(httpError): void {
    if (!httpError) {
      return;
    }

    const statusType = HttpUtil.getHttpStatusType(httpError.status);

    switch (statusType) {
      case HttpStatusType.ClientError:
        this.httpErrorInClient(httpError);
        break;
      case HttpStatusType.ServerError:
        this.httpErrorInServer(httpError);
        break;
      default:
        throw new Error('Get invalid status code when using httpErrorHandler method.');
    }
  }


  /**
 * 
 */
  private httpErrorInServer(httpError): void {
    let error = httpError.data;
    if (typeof (error) === 'string') {
      try {
        error = JSON.parse(error);
      } catch (e) { }
    }
    if (!error || error.Level == null || error.Level == undefined) { // error
      this.notifyService.error(httpError.message);
      return;
    }

    const getNotifyServiceMethodName: String = this.getNotifyServiceMethodName(error.Level);

    this.notifyService[`${getNotifyServiceMethodName}`](error.Message);
  }

  /**
   * 
   */
  private httpErrorInClient(httpError): void {
    if (!httpError) {
      return;
    }
    const url = httpError.config && httpError.config.url && httpError.config.url;
    const status = httpError.status && httpError.status;
    this.notifyService.error(`${url} ${status}`);
  }


  /**
 * 
 * @param exceptionLevel 
 */
  public getNotifyServiceMethodName(exceptionLevel): String {
    let exception;
    switch (exceptionLevel) {
      case ExceptionLevel.Info:
        exception = 'info';
        break;
      case ExceptionLevel.Warning:
        exception = 'warning';
        break;
      case ExceptionLevel.Error:
        exception = 'error';
        break;
      case ExceptionLevel.Fatal:
        exception = 'fatal';
        break;
      default:
        // 
        exception = 'error';
        break;
    }

    return exception;
  }
}

export { ExceptionService };
