import { ViewModelContext } from '@farris/mobile-devkit';

/**
 * 
 * @summary
 * 
 */
class DataUtil {

  /**
   * BindingPathEntityPath
   */
  public static convertBindingPathToEntityPath(bindingPath: string, viewModelContext: ViewModelContext): string {
    const rid = viewModelContext.bindingData.list.currentId; // rootid
    let path = '/' + rid;

    const subPaths = bindingPath.split('/');
    if (subPaths.length > 0) {
      for (let index = 1; index < subPaths.length - 1; index++) {
        const subPath = subPaths[index];
        const subData = viewModelContext.bindingData[subPath];
        if (!subData || !subData.currentId) {
          throw Error(`${subData}`);
        }
        path += `/${subPath}/${subData.currentId}`;
      }
    }
    path += '/' + subPaths[subPaths.length - 1];

    return path;
  }

  public static clearFromValidateType(viewModelContext: ViewModelContext): void {
    const contexts = viewModelContext.appContext.viewModelContextManager.getContexts();
    const contextsChild = contexts.filter(item => { return (item.parent && viewModelContext.id === item.parent.id) })
    if (contextsChild.length >= 1) {
      contextsChild.forEach(item => {
        item.form.resetFieldsValidate()
      })
    }
  }

  public static stringTransformArray(parame): any[] {
    if (this.parameIsThreeBoss(parame)) {
      return []
    } else {
      return JSON.parse(parame);
    }
  }

  /**
   *  ''  undefined  null
   */
  public static parameIsThreeBoss(parame) {
    if (parame === '' || parame === undefined || parame === null) {
      return true
    } else {
      return false
    }
  }
}

export { DataUtil };

