import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { Entity, ViewModelContext } from '@farris/mobile-devkit';

import { BaseDataService } from './base-data.service';
import { DataUtil } from './data-util';

/**
 * 
 */
export class CreateDataService extends BaseDataService {

  constructor(viewModelContext: ViewModelContext) {
    super(viewModelContext);
  }

  /**
   * 
   */
  public create(): Observable<Entity> {
    this.befRepository.entityCollection.clear();
    DataUtil.clearFromValidateType(this.viewModelContext);
    this.loadingService.show();
    const create$ = this.befRepository.createEntity();
    return create$.pipe(
      tap(
        () => {
          this.loadingService.hide();
        },
        (exception: any) => {
          this.loadingService.hide();
        }
      )
    );
  }

  /**
   * 
   */
  public createByPath(path: string): Observable<Entity> {
    this.loadingService.show();
    // 
    const entityPath = DataUtil.convertBindingPathToEntityPath(path, this.viewModelContext);
    const append$ = this.befRepository.appendEntityByPath(entityPath);
    const result$ = append$.pipe(
      tap(
        (e) => {
          this.viewModelContext.uiState.setPropertyValue('$isAdd', { status: true, id: e['id'], path, child: { ...e.data } });
          this.loadingService.hide();
        },
        (exception: any) => {
          this.loadingService.hide();
        }
      )
    );

    return result$;
  }

}
