import { ViewModelContext } from '@farris/mobile-devkit';
import { EntityTraversingService } from './entity-traversing.service';
import { EntityManipulationService } from './entity-manipulation.service';
import { EntityAggregationService} from './entity-aggregation.service';

/**
 * 
 * @scope FrameComponent
 */
class EntityService {

  private traversingService: EntityTraversingService;

  private manipulationService: EntityManipulationService;

  private aggregationService: EntityAggregationService;

  private viewModelContext: ViewModelContext;

  constructor(viewModelContext: ViewModelContext) {
    this.viewModelContext    = viewModelContext;
    this.traversingService   = new EntityTraversingService(viewModelContext);
    this.manipulationService = new EntityManipulationService(viewModelContext);
    this.aggregationService  = new EntityAggregationService(viewModelContext);
  }

  /**
   * path
   */
  public getPropValue(path: string[]): any {
    return this.traversingService.getPropValue(path);
  }

  /**
   * path
   */
  public setPropValue(path: string[], value: any): void {
    return this.manipulationService.setPropValue(path, value);
  }

  /**
   * pathEntity
   */
  public getEntityData(path: string[]): any {
    return this.traversingService.getEntityData(path);
  }

  /**
   * pathEntityList
   */
  public getEntityListData(path: string[]): any {
    return this.traversingService.getEntityListData(path);
  }

  /**
   * pathEntityList
   */
  public count(path: string[]): number {
    return this.aggregationService.count(path);
  }

  /**
   * path
   */
  public sum(path: string[]): number {
    return this.aggregationService.sum(path);
  }

  /**
   * path
   */
  public avg(path: string[]): number {
    return this.aggregationService.avg(path);
  }

  /**
   * path
   */
  public max(path: string[]): number {
    return this.aggregationService.max(path);
  }

  /**
   * path
   */
  public min(path: string[]): number {
    return this.aggregationService.min(path);
  }
}

export { EntityService };
