import { Observable, of, EMPTY } from 'rxjs';
import { tap, switchMap } from 'rxjs/operators';
import { Entity, HttpMethods, HttpRequestConfig, ViewModelContext } from '@farris/mobile-devkit';

import { DataUtil } from './data-util';
import { BaseDataService } from './base-data.service';


/**
 * 
 */
export class RemoveDataService extends BaseDataService {

  constructor(viewModelContext: ViewModelContext) {
    super(viewModelContext);
  }


  /**
   * 
   */
  public removeById(id: string): Observable<any> {

    const result$ = this.dialogService.confirm('').pipe(
      switchMap(val => {
        if (val === false) {
          return EMPTY;
        }
        if (this['context'] && this['context'].eventParams && this['context'].eventParams.swipecellClose) {
          this['context'].eventParams.swipecellClose();
        }
        return this.cancelChanges(id);
      }
      ))
    return result$
  }

  /**
   * 
   * @param id 
   */

  private cancelChanges(id: string): Observable<any> {
    this.loadingService.show();
    const remove$ = this.befRepository.removeEntityAndSaveById(id);
    return remove$.pipe(
      tap(
        () => {
          this.notifyService.success('');
          this.loadingService.hide();
        },
        () => {
          this.loadingService.hide();
          this.notifyService.error('');
        }
      )
    );
  }


  /**
   * 
   */
  public removeByIds(ids: string[]): Observable<boolean> {
    // ListViewUIStateids
    if (!ids) {
      const selectedItems = this['context'].eventParams;
      ids = selectedItems.map((selectedItem) => {
        return selectedItem.id;
      });
    }

    const multiRemove$ = this.dialogService.confirm('').pipe(
      switchMap(val => {
        if (val === false) {
          return EMPTY;
        }
        this.loadingService.show();
        return this.befRepository.removeEntitiesByIds(ids);
      }));
    return multiRemove$.pipe(
      tap(
        () => {
          this.loadingService.hide();
          this.notifyService.success('');
        },
        (exception: any) => {
          this.loadingService.hide();

          // this.exceptionService.show('', exception);
        }
      )
    );
  }

  /**
   * 
   */
  public removeByPathAndId(path: string, id: string): Observable<boolean> {
    const result$ = this.dialogService.confirm('').pipe(
      switchMap(val => {
        if (val === false) {
          return EMPTY;
        }
        if (this['context'] && this['context'].eventParams && this['context'].eventParams.swipecellClose) {
          this['context'].eventParams.swipecellClose();
        }
        return this.removeByPathAndIdChanges(path, id);
      }
      ));
    return result$;
  }


  public removeByPathAndIdChanges(path: string, id: string): Observable<boolean> {
    this.loadingService.show();
    const entityPath = DataUtil.convertBindingPathToEntityPath(path, this.viewModelContext);
    const remove$ = this.befRepository.removeEntityByPath(entityPath, id);
    return remove$.pipe(
      tap(
        () => {
          this.loadingService.hide();
          this.notifyService.success('');
        },
        (error: any) => {
          this.loadingService.hide();
          console.error(error);
        }
      )
    );
  }

  /**
   * 
   */
  public removeByPathAndIds(path: string, ids: string[]): Observable<boolean> {
    throw new Error('Not Implemented');
  }

  /**
   * 
   */
  public removeByBusinessIds(url: string, ids: string[]): Observable<boolean> {
    // ListViewUIStateids
    if (!ids) {
      const selectedItems = this['context'].eventParams;
      ids = selectedItems.map((selectedItem) => {
        return selectedItem.id;
      });
    }
    const params = {
      ids: ids.join(',')
    };
    const requestConfig: HttpRequestConfig = {
      params: params,
      body: {
        dataChange: [],
        variableChange: null
      }
    };
    return this.befRepository.apiProxy.request(HttpMethods.PUT, url, requestConfig, true);
    // this.loadingService.show();
    // const multiRemove$ = this.dialogService.confirm('').pipe(
    //   switchMap(val => {
    //     if (val === false) {
    //       return EMPTY;
    //     }
    //   }));
    // return multiRemove$.pipe(
    //   tap(
    //     () => {
    //       this.loadingService.hide();
    //     },
    //     (exception: any) => {
    //       this.loadingService.hide();
    //     }
    //   )
    // );
  }


  /**
   * 
   */
  public removeByBusinessPathAndId(url: string, path: string, id: string): Observable<boolean> {
    const result$ = this.dialogService.confirm('').pipe(
      switchMap(val => {
        if (val === false) {
          return EMPTY;
        }
        return this.befRepository.apiProxy.request(HttpMethods.PUT, url);
      }
      ));
    return result$;
  }


  /**
   * 
   * @returns 
   */
  public ClearStatusAfterchildCardSave(): Observable<any> {
    this.viewModelContext.uiState.setPropertyValue('$isAdd', null);
    this.viewModelContext.uiState.setPropertyValue('$childEntity', null);
    return of(true)
  }

}
