import { IJsBridgeStrategy } from './decorators';
import { CCworkService } from './ccwork-service';
import { CloudPlusService } from './cloud-plus-service';
class JsBridgeStrategyFactory {

    create(): IJsBridgeStrategy {
        if (navigator.userAgent.toLowerCase().search('ccwork') !== -1) {
            window.top['FARRIS_JSBridge'] = ''
            return new CCworkService()
        } else if( navigator.userAgent.toLowerCase().search('emmcloud') !== -1) {
            window.top['FARRIS_JSBridge'] = ''
            return new CloudPlusService()
        } else {
            window.top['FARRIS_JSBridge'] = ''
            return new CloudPlusService()
        }
    }
}

export { JsBridgeStrategyFactory }