import { Observable, of, EMPTY } from 'rxjs';
import { tap } from 'rxjs/operators';
import { HttpClient, ViewModelContext } from '@farris/mobile-devkit';
import { LoadingService } from '../ui-services/index';


export class DiscussionGroupService {
    constructor(
        private httpSvc: HttpClient,
        private viewModelContext: ViewModelContext,
        private loadingService: LoadingService,
    ) {
    }

    private get params() {
        return this['context'] && this['context']['eventParams'] || {};
    }

    /**
     * 
     * @param id id
     * @param summary 
     * @param configId id
     * @param text 
     * @param visibility ()
     * @param parentId 
     * @returns 
     */
    public addComment(id?: string, summary?: string, configId?: string, text?: string, visibility?: string, parentId?: string): Observable<any> {
        id = id || this.viewModelContext && this.viewModelContext.bindingData.list.currentId || null;
        if (!id) {
            return EMPTY;
        }

        const url = '/api/runtime/comment/v1.0/bill-comment/comment';
        const body = this.buildAddCommentParam(id, text, parentId, summary, visibility, configId);
        this.loadingService.show()
        return this.httpSvc.post(url, body, {}).pipe(
            tap(() => {
                this.loadingService.hide()
            })
        )
    }

    private buildAddCommentParam(id: string, text: string, parentId: string, summary: string, visibility: string, configId: string) {
        if (typeof text === 'undefined') {
            text = this.params.text;
        }
        if (typeof parentId === 'undefined') {
            parentId = this.params.replyUser && this.params.replyUser.id;
        }
        if (typeof visibility === 'undefined') {
            visibility = this.params.visibility || 'ALL';
        }
        return {
            'bill': {
                'billId': id,
                'configId': configId,
                'summary': summary
            },
            'comment': {
                'billId': id,
                'configId': configId,
                'parentId': parentId || null,
                'text': text,
                'visibility': visibility
            }
        }
    }

    /**
     * 
     * @param id id
     * @param configId id
     * @param pageIndex 
     * @param pageSize 
     * @returns 
     */
    public queryComments(id: string, configId: string, pageIndex?: number | null, pageSize?: number | null): Observable<any> {
        id = id || this.viewModelContext && this.viewModelContext.bindingData.list.currentId || null;
        if (!id) {
            return EMPTY;
        }

        const url = this.buildQueryCommentsUrl(id, pageIndex, pageSize, configId);
        this.loadingService.show()
        return this.httpSvc.get(url, {}).pipe(
            tap((e) => {
                this.loadingService.hide()
                this.viewModelContext.uiState.setPropertyValue('discussionListData', e);
            })
        )
    }

    private buildQueryCommentsUrl(id: string, pageIndex: number | null, pageSize: number | null, configId: string) {
        if (typeof pageIndex === 'undefined' || pageIndex === null) {
            pageIndex = this.params.pageIndex || 0;
        }
        if (typeof pageSize === 'undefined' || pageSize === null) {
            pageSize = this.params.pageSize || 999;
        }
        return `/api/runtime/comment/v1.0/bill-comment/comment/byBill?configId=${configId}&billId=${id}&pageSize=${pageSize}&pageIndex=${pageIndex}`;
    }


    /**
     * 
     */
    public setReplyUser() {
        this.viewModelContext.uiState.setPropertyValue('replyUser', this.params);
    }

    public queryFrequentAtUsers(pageIndex?: number | null, pageSize?: number | null) {
        const params: string[] = [];
        params.push(`pageSize=${pageSize || 20}`);
        params.push(`pageIndex=${pageIndex || 0}`);
        const url = `/api/runtime/comment/v1.0/bill-comment/frequentAtUsers?${params.join('&')}`;
        this.loadingService.show()
        return this.httpSvc.get(url, {}).pipe(
            tap((e) => {
                this.loadingService.hide()
                const personnelsData = {
                    pageInfo: {
                        pageSize: e.pageSize,
                        pageIndex: e.pageIndex
                    },
                    total: e.totalCount,
                    items: e.users
                }
                this.viewModelContext.uiState.setPropertyValue('personnelsData', personnelsData);
            })
        )
    }

    public queryAtUsers(user?: string, pageIndex?: number | null, pageSize?: number | null) {
        const params: string[] = [];
        if (user) {
            params.push(`param=${user}`);
        }
        params.push(`pageSize=${pageSize || 100}`);
        params.push(`pageIndex=${pageIndex || 0}`);
        const url = `/api/runtime/comment/v1.0/bill-comment/atUser?${params.join('&')}`;
        this.loadingService.show()
        return this.httpSvc.get(url, {}).pipe(
            tap((e) => {
                this.loadingService.hide()
                const personnelsData = {
                    pageInfo: {
                        pageSize: e.pageSize,
                        pageIndex: e.pageIndex
                    },
                    total: e.totalCount,
                    items: e.users
                }
                this.viewModelContext.uiState.setPropertyValue('personnelsData', personnelsData);
            })
        )
    }
}
