import { Observable, from, EMPTY } from 'rxjs';
import { Dialog } from '@farris/mobile-ui';

class DialogService {

  /**
   * 
   * @summary
   * type=info|success|warning|error
   */
  alert(message: string, options?: any): Observable<boolean> {
    const promise$: Promise<boolean> = new Promise((resolve, reject) => {
      Dialog.alert({
        title: '',
        message: message,
        cancelText: '',
        confirmText: '',
        onConfirm: () => { resolve(true) },
        onCancel: () => { resolve(false) },
        ...options
      });
    });
    return from(promise$);
  }

  /**
   * 
   */
  confirm(message: string): Observable<boolean> {
    const confirmResult$: Promise<boolean> = this.confirmPromise(message);
    return from(confirmResult$);
  }

  /**
   * 
   */
  show(options: any): void {
    this.confirmPromise(options);
  }

  /**
   * 
   * @param title 
   * @param options 
   */
  prompt(title: string, options?: any): Observable<string> {
    const promise$: Promise<string> = new Promise((resolve, reject) => {
      const vm = Dialog.prompt({
        title: title,
        confirmText: '',
        onConfirm: () => {
          resolve(vm.$data.promptText);
        },
        onCancel: () => { resolve(''); },
        onShow: () => {
          console.log('');
        },
        ...options
      });
    });
    return from(promise$);
  }

  /**
   * Promise
   */
  private confirmPromise(message?: string): Promise<boolean> {
    return new Promise((resolve, reject) => {
      Dialog.confirm({
        message: message,
        onConfirm: () => { resolve(true); },
        onCancel: () => { resolve(false); },
      })
    });
  }
}

export { DialogService };
