import { ViewModelContext, Entity } from '@farris/mobile-devkit';
import { BefRepository } from '@farris/mobile-bef';

/**
 * VO
 */
class VoVariableService {

  /**
   * 
   */
  private viewModelContext: ViewModelContext;


  /**
   * 
   */
  constructor(viewModelContext: ViewModelContext) {
    this.viewModelContext = viewModelContext;
  }

  /**
   * 
   */
  public setValue(name: string, value: any): void {
    const befRepository = this.viewModelContext.repository as BefRepository<Entity>;
    befRepository.variableManager.setValue(name, value);
  }

  /**
   * 
   */
  public getValue(name: string): void {
    const befRepository = this.viewModelContext.repository as BefRepository<Entity>;
    befRepository.variableManager.getValue(name);
  }

}

export { VoVariableService };
