import { Loading } from '@farris/mobile-ui';

export class LoadingService {

    constructor() {
        this.registerService();
    }

    /**
     * Loading
     */
    show(message?: string, options?: any): void {
        (Loading as any).show(message, options);
    }

    /**
     * Loading
     */
    hide(): void {
        (Loading as any).hide();
    }
    
    /**
     * LoadingService
     */
    private registerService() {
        const services: any[] = window['DEVKIT_LOADING_SERVICE'] || [];
        services.push(this);
        window['DEVKIT_LOADING_SERVICE'] = services;
    }


}
