// import { Router } from 'vue-router';
import { Observable } from 'rxjs';
import { tap, map } from 'rxjs/operators';
import { ViewModelContext, HttpClient } from '@farris/mobile-devkit';
import { RtcOptions, BeforeNavigateReponseBody, BeforeNavigateRequestBody } from './types/index';
import { JsBridgeService } from './js-bridge-service/index';




/**
 * 
 */
class RouterService {

  /**
   * ViewModel
   */
  private viewModelContext: ViewModelContext;

  /**
   * VueRouter
   */
  private router: any;

  /**
   * 
   */
  constructor(viewModelContext: ViewModelContext, router: any, private jsBridgeService: JsBridgeService) {
    this.viewModelContext = viewModelContext;
    this.router = router;
  }

  /**
   * 
   * @param path   
   * @param params 
   */
  navigate(path: string, queryParams: any = {}, backParams: any = {}, backPath?: string) {
    if (!path) return

    queryParams = this.ParamsFromStringToObject(queryParams);
    backParams = this.ParamsFromStringToObject(backParams);

    const pathIndex = this.sessionStorageSaveHistory(path, backPath, backParams)

    // 
    if (path && pathIndex >= 0) {
      let urlPath = this.splicePath(path, queryParams);
      window.location.href = urlPath;
      return false;
    }

    window['MOBILE_ORIGIN_BACK'] && window['MOBILE_ORIGIN_BACK'].reflushOriginGoback();

    this.router.push({
      path: path,
      query: queryParams
    });
  }

  private removeParams(pathIndex, path) {
    let end = path.search('\\?');
    if (end = -1) {
      end = path.length;
    }
    return path.slice(pathIndex, end);
  }

  private getsessionStorageSaveHistoryKey(path) {
    const pathIndex = path.search('/apps');
    // 
    if (path && pathIndex >= 0) {
      return this.removeParams(pathIndex, path);
    }
    return this.router.options.history.base + path;
  }

  private sessionStorageSaveHistory(path, backPath, backParams) {
    const pathIndex = path.search('/apps');

    const key = this.getsessionStorageSaveHistoryKey(path);
    let historyObject = {}
    //
    if (backPath) {
      const backPathIndex = path.search('/apps');
      //
      if (backPathIndex >= 0) {
        historyObject = {
          routerWay: 'href',
          path: backPath,
          query: backParams
        }
      } else {
        historyObject = {
          routerWay: 'Router',
          path: backPath,
          query: backParams
        }
      }
      sessionStorage.setItem(key, JSON.stringify(historyObject))
      return pathIndex;
    } else {
      let currentPath = this.router.currentRoute.value.path;
      // 
      if (path && pathIndex >= 0) {
        //
        currentPath =  this.router.options.history.base + this.router.options.history.location;
        //
        historyObject = {
          routerWay: 'href',
          path: currentPath,
          query: backParams
        }
      } else {
        // 
        historyObject = {
          routerWay: 'Router',
          path: currentPath,
          query: backParams
        }
      }
      sessionStorage.setItem(key, JSON.stringify(historyObject))
      return pathIndex;
    }
  }

  /**
   * 
   */
  navigateForRtc(path: string, queryParams: any = {}, backParams: any = {}, backPath?: string) {

    if (!queryParams || !queryParams.metadataId) {
      this.navigate(path, queryParams, backParams, backPath);
    }

    const rtcOptions: RtcOptions = {
      metadataId: queryParams.metadataId,
      withDims: false,
      dim1: queryParams.dim1,
      dim2: queryParams.dim2
    };
    delete queryParams['metadataId'];
    delete queryParams['dim1'];
    delete queryParams['dim2'];

    const correctRtcOptions$ = this.correctRtcOptions(rtcOptions);
    const extendedPath$ = correctRtcOptions$.pipe(
      map((correctedRtcOptions: RtcOptions) => {
        return this.getExtendPath(path, correctedRtcOptions);
      })
    );

    const result$ = extendedPath$.pipe(
      tap((extendedPath: string) => {
        this.navigate(extendedPath, queryParams, backParams);
      })
    );

    result$.subscribe();
  }

  /**
   * 
   * @summary
   * 1widthDims=false, 
   * 2public
   */
  private correctRtcOptions(rtcOptions: RtcOptions): Observable<RtcOptions> {
    const dim1 = rtcOptions.dim1 || 'public';
    const dim2 = rtcOptions.dim2 || 'public';
    const metadataId = rtcOptions.metadataId;
    const httpClient = this.viewModelContext.injector.get<HttpClient>(HttpClient);

    const url = '/api/runtime/bcc/v1.0/template/beforeNavigate';
    const body: BeforeNavigateRequestBody = {
        isRootMetadata: true,
        metadataId: metadataId,
        dim1: dim1,
        dim2: dim2,
    };
    const beforeNavigate$ = httpClient.post(url, body, null).pipe(
      map((result: BeforeNavigateReponseBody) => {
        const correctedRtcOptions: RtcOptions = {
          metadataId: result.metadataId,
          withDims: result.withDims === 1 ? true : false,
          dim1: result.dim1,
          dim2: result.dim2
        };

        return correctedRtcOptions;
      })
    );
    return beforeNavigate$;
  }

  /**
   * 
   */
  private getExtendPath(path: string, rtcOptions: RtcOptions): string {

    if (rtcOptions.withDims === false) {
      return path;
    }

    const arrPath = path.split('index.html');
    const append = `${rtcOptions.dim1}/${rtcOptions.dim2}/index.html`.toLowerCase();
    const extendPath = arrPath.join(append);
    return extendPath;
  }

  /**
   * url
   * @param path 
   * @param params 
   * @returns 
   */
  private splicePath(path: string, params: any) {
    let urlPath = path
    const end = urlPath.search('\\?');
    if (end > -1) {
      urlPath = urlPath.slice(0, end)
    }
    const keys = Object.keys(params);
    if (keys.length > 0) {
      urlPath = urlPath + '?';
      keys.forEach(element => {
        urlPath = urlPath + element + '=' + params[`${element}`] + '&'
      });
      urlPath = urlPath.slice(0, urlPath.length - 1)
    }
    return urlPath;
  }

  private ParamsFromStringToObject(params: any): object {

    if (Object.prototype.toString.call(params) === '[object Object]') {
      return params;
    }

    try {
      params = JSON.parse(params);
    } catch (error) {
      if (!params) {
        params = {}
      } else {
        params = {}
        console.error('paramsJSON')
      }
    }

    if (Object.prototype.toString.call(params) !== '[object Object]') {
      return {};
    }

    return params;
  }

  /**
 * 
 * @param path   
 * @param params 
 */
  navigateReplace(path: string, queryParams: any = {}) {
    if (typeof queryParams === 'string') {
      queryParams = JSON.parse(queryParams);
    }
    this.router.replace({
      path: path,
      query: queryParams
    });
  }

  /**
   * 
   */
  goBack(params) {
    params = this.ParamsFromStringToObject(params);
    const currentPath = this.router.options.history.base + this.router.currentRoute.value.path;
    let need_go_back = sessionStorage.getItem(currentPath);
    if (!need_go_back) {
      if (window.top.location.pathname.indexOf('mobiletaskcenter') > -1) {
        return;
      }
      this.jsBridgeService.closeWindow()
      return
    }
    const keys = Object.keys(params);

    need_go_back = JSON.parse(need_go_back);
    if (need_go_back && need_go_back['routerWay'] === 'Router') {
      let query = need_go_back['query'];
      if (keys.length > 0) {
        query = { ...query, ...params }
      }
      this.router.push({
        query,
        path: need_go_back['path'],
      });
    }
    if (need_go_back && need_go_back['routerWay'] === 'href') {
      let query = need_go_back['query'];
      if (keys.length > 0) {
        query = { ...query, ...params }
      }
      let path = need_go_back['path'];

      path = this.splicePath(path, query);

      window.location.href = path;
    }
  }

  /**
   * 
   */
  mappingToUIState() {
    const routerState = {
      params: this.router.currentRoute['value'].params,
      queryParams: this.router.currentRoute['value'].query
    };
    this.viewModelContext.uiState['routerState'] = routerState;
  }
}

/**
 * Token
 * @todoVueRouter
 */
const ROUTER_INSTANCE_TOKEN = '@farris/mobile-command-services/ROUTER_INSTANCE_TOKEN';

export { RouterService, ROUTER_INSTANCE_TOKEN };
