import { Entity, ViewModelContext } from '@farris/mobile-devkit';
import { BefRepository } from '@farris/mobile-bef';
import { EntityService } from '../entity-services/index';
import { LoadingService, ToastService, NotifyService, DialogService } from '../ui-services/index';
import { ExceptionService } from '../exception-services/index';
import { CommandService } from '../command.service';

/**
 * 
 */
class BaseDataService {

  protected loadingService: LoadingService;

  protected toastService: ToastService;

  protected notifyService: NotifyService;

  protected dialogService: DialogService;

  protected exceptionService: ExceptionService;

  protected befRepository: BefRepository<Entity>;

  protected entityService: EntityService;

  constructor(protected viewModelContext: ViewModelContext) {
    this.init();
  }

  private init() {
    this.initServices();
  }

  private initServices() {
    this.loadingService = this.getService<LoadingService>(LoadingService);
    this.toastService = this.getService<ToastService>(ToastService);
    this.notifyService = this.getService<NotifyService>(NotifyService);
    this.dialogService = this.getService<DialogService>(DialogService);
    this.exceptionService = this.getService<ExceptionService>(ExceptionService);
    this.entityService = this.getService<EntityService>(EntityService);
    this.befRepository = this.viewModelContext.repository as BefRepository<Entity>;
  }

  /**
   * 
   */
  getService<T>(token: any): T {
    const injector = this.viewModelContext.injector;
    return injector.get<T>(token);
  }

}

export { BaseDataService };
