import { Observable, of, EMPTY } from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';
import { Entity, ViewModelContext } from '@farris/mobile-devkit';
import { BefRepository } from '@farris/mobile-bef';
import { BaseDataService } from './base-data.service';

/**
 * 
 */
export class UpdateDataService extends BaseDataService {

  constructor(viewModelContext: ViewModelContext) {
    super(viewModelContext);
  }

  /**
   * 
   */
  public update(id): Observable<Entity> {
    this.loadingService.show();
    const update$ = this.befRepository.updateEntityById(id);
    const result$ =  update$.pipe(
      tap(
        () => {
          this.loadingService.hide();
        },
        (exception: any) => {
          this.loadingService.hide();
          // this.exceptionService.show('', exception);
        }
      )
    );

    return result$;
  }

}
