import { ViewModelContext, BindingData, BindingList, BindingObject } from '@farris/mobile-devkit';

/**
 * 
 */
class EntityTraversingService {

  private get bindingData(): BindingData {
    return this.viewModelContext.bindingData;
  }

  constructor(private viewModelContext: ViewModelContext) {
  }

  /**
   * 
   */
  public getPropValue(path: string[]): any {
    return this.bindingData.getValue(path);
  }

  /**
   * 
   */
  public getEntityData(path: string[]): any {
    let bindingObj: BindingObject;
    const bindingListOrObject = this.bindingData.getValue(path);
    if (bindingListOrObject instanceof BindingList === true) {
      bindingObj = bindingListOrObject.currentItem;
    } else {
      bindingObj = bindingListOrObject;
    }
    return bindingObj.toJSON();
  }

  /**
   * 
   */
  public getEntityListData(path: string[]): any[] {
    const bindingList = this.bindingData.getValue(path) as BindingList;
    return bindingList.toJSON();
  }

}

export { EntityTraversingService };
