
import { Subject } from 'rxjs';

import { StartProcessPayload, FlowNodeInfo } from '../../entity/start-process-payload';
import { SimulateResponse, SimulationPath } from '../../entity/simulate-response';

import { TaskService } from '../../service/task.service';
import { SubmitApprove } from '@farris/mobile-business-ui';


export class ProcessSelectorService {

  constructor(
    private taskService: TaskService
  ) { }

  destProcesses: { id: string, name: string }[];//
  destNodes: { id: string, name: string }[];//

  processPlaceholder: string = '';//placeholder
  nodePlaceholder: string;//placeholder
  response: SimulateResponse;//

  assignInfos = [];

  selectedProcessId: string;//ID
  selectedNodeId: string; //IDid

  selectedValues = ['clear'];

  payload = new StartProcessPayload;//payload
  subject = new Subject();
  processReadOnly: boolean = false;//
  nodeReadOnly: boolean = true;//

  vm: any;//

  setParam(param: StartProcessPayload) {
    this.payload.id = param.id;
    this.payload.processDefinitionId = param.processDefinitionId;
    this.payload.processDefinitionKey = param.processDefinitionKey;
    this.payload.bizDefKey = param.bizDefKey;
    this.payload.dataModelId = param.dataModelId;
    this.payload.dataId = param.dataId;
    this.payload.name = param.name;
    this.payload.startUserId = param.startUserId;
    this.payload.nextFlowNodes = [];
    this.startProcessSimulate(this.payload);
  }

  startProcessSimulate(payload: StartProcessPayload) {
    this.taskService.startProcessSimulate(payload).subscribe((response: SimulateResponse) => {
      this.destProcesses = response.procDefs.map(process => ({ id: process.procDefId, name: process.procDefName }));
      if (this.destProcesses.length === 1) {
        this.processReadOnly = true;
        this.selectedProcessId = response.procDefs[0].procDefId;
        this.vm = (SubmitApprove as any).confirm(
          {
            process: this.selectedProcessId,
            processData: this.destProcesses,
            processOc: this.selectProcessChange.bind(this),
            processReadFlag: this.processReadOnly,
            processHolder: this.processPlaceholder,
            nextNodeReadFlag: this.nodeReadOnly,
            submit: this.submit.bind(this),
            cancel: this.back.bind(this)
          }
        )
        this.response = response;
        this.setNextNode(response);
      } else {
        this.vm = (SubmitApprove as any).confirm(
          {
            processData: this.destProcesses,
            processOc: this.selectProcessChange.bind(this),
            processReadFlag: this.processReadOnly,
            processHolder: this.processPlaceholder,
            nextNodeReadFlag: this.nodeReadOnly,
            submit: this.submit.bind(this),
            cancel: this.back.bind(this)
          }
        )
      }
    }, error => {
      this.subject.next({ type: 'error', message: error && error.response && error.response.data && error.response.data.Message || '' });
    })
  }

  /**
 * 
 * @param data
 */
  selectProcessChange(data: any) {
    this.clearHelp();
    this.clearNode();
    this.selectedProcessId = data;

    var payload = new StartProcessPayload();
    payload.processDefinitionId = this.selectedProcessId;
    payload.dataId = this.payload.dataId;

    // const load = this.loadingService.show();
    this.taskService.startProcessSimulate(payload).subscribe((response: SimulateResponse) => {
      // load.close();
      this.response = response;
      this.setNextNode(response);
    }, error => {
      //load.close();
      this.subject.next({ type: 'error', message: error && error.response && error.response.data && error.response.data.Message || '' });
    })
  }


  getsimulationPathsIndexData(data) {
    return this.destNodes.findIndex(item => item.id === data)
  }

  /**
 * 
 */
  selectNodeChange(data: any) {
    this.clearHelp();
    this.clearAssignInfos();
    this.selectedNodeId = data;
    this.vm.open({
      nextNode: this.selectedNodeId,
    })
    //const nodeNames = data.data.name.split('');
    const index = this.getsimulationPathsIndexData(data);
    if (this.response.simulationPaths[index].nodeInfoList.every(node => node.resolved === true)) {//
      this.setAssignInfo(this.response.simulationPaths[index]);
    } else {//
      let payload = new StartProcessPayload();
      payload.processDefinitionId = this.selectedProcessId;
      payload.dataId = this.payload.dataId;
      payload.nextFlowNodes = [];

      this.selectedNodeId.split('').forEach((nodeId, index) => {
        let nextFlowNode = new FlowNodeInfo();
        nextFlowNode.activityDefinitionId = nodeId;
        //nextFlowNode.activityDefinitionName = nodeNames[index];
        payload.nextFlowNodes.push(nextFlowNode);
      });

      //const load = this.loadingService.show();
      this.taskService.startProcessSimulate(payload).subscribe((response: SimulateResponse) => {
        //load.close();
        this.setAssignInfo(response.simulationPaths[0]);//simulationPaths[0]
      }, error => {
        //load.close();
        this.subject.next({ type: 'error', message: error && error.response && error.response.data && error.response.data.Message || '' });
      })
    }
  }

  /**
 * 
 */
  private setNextNode(response: SimulateResponse) {
    this.destNodes = response.simulationPaths && response.simulationPaths.map(path => ({
      id: path.nodeInfoList.map(node => node.activityDefinitionId).join(''),
      name: path.nodeInfoList.map(node => node.activityDefinitionName).join('')
    }));
    //-1
    const defaultIndex = response.simulationPaths.findIndex(a => a.default);
    if (defaultIndex > -1) {
      this.destNodes.length === 1 ? (this.nodeReadOnly = true) : (this.nodeReadOnly = false);
      this.selectedNodeId = this.destNodes[defaultIndex].id;
      this.setAssignInfo(response.simulationPaths[defaultIndex]);
    } else {
      this.selectedNodeId = '';
      this.nodeReadOnly = false;
      this.nodePlaceholder = '';
    }
    this.vm.open({
      nextNodeOc: this.selectNodeChange.bind(this),
      nextNode: this.selectedNodeId,
      nextNodeData: this.destNodes,
      nextNodeReadFlag: this.nodeReadOnly,
      nextNodeHolder: this.nodePlaceholder
    })
  }

  /**
   * 
   */
  private setAssignInfo(simulationPaths: SimulationPath) {
    this.assignInfos = [];
    this.selectedNodeId.split('').forEach(nodeId => {
      const node = simulationPaths.nodeInfoList.find(node => node.activityDefinitionId === nodeId);
      if (node) {
        const selectedAssignees = node.selectedAssignees;
        let assigneeReadFlag = false;
        let assignee = '';
        let assigneeHolder = '';
        if (selectedAssignees && selectedAssignees.length >= 1) {
          assigneeReadFlag = true;
          assigneeHolder = "";
          selectedAssignees.forEach(user => {
            assignee = assignee + user.name + ', ';
          })
          assignee = assignee.slice(0, assignee.length - 2)
        }
        if (node.anonymous) {
          assignee = node.anonymousText === '' ? '' : node.anonymousText && node.anonymousText
        }
        this.assignInfos.push({
          label: `${this.selectedNodeId && this.selectedNodeId.split('').length > 1 ? node.activityDefinitionName : ''}`,
          selectedAssignees,
          assigneeReadFlag,
          assignee,
          assigneeHolder,
          assigneeInfo: { ...node.assigneeInfo, assigneeUsers: { items: (node.assigneeInfo && node.assigneeInfo.assigneeUsers) || [] } },
          assignees: node.selectedAssignees.map(user => { return { id: user.id, name: user.name } }),
          activityName: node.activityDefinitionName,
          dependency: node.dependency,
          anonymous: node.anonymous,
          anonymousText: node.anonymousText
        })
      } else {
        this.assignInfos.push({
          selectedAssignees: [],
          assigneeInfo: null,
          assignees: [],
          activityName: '',
          dependency: '',
          anonymous: node.anonymous,
          anonymousText: node.anonymousText
        })
      }
    })
    this.vm.open({
      assigneeOc: this.assigneeOc.bind(this),
      assignInfos: this.assignInfos
    })
  }

  // 
  assigneeOc(data: any, index: number) {
    this.assignInfos[index].assignees = data.items;
    this.selectedValues[index] = data.value;
    this.vm.open({
      selectedValues: this.selectedValues
    })
  }

  clearHelp() {
    this.vm.open({
      selectedValues: ['clear']
    })
  }

  // 
  clearNode() {
    this.selectedNodeId = '',
      this.destNodes = [],
      this.nodeReadOnly = true,
      this.nodePlaceholder = '',
      this.assignInfos = [];
    this.vm.open({
      nextNode: this.selectedNodeId,
      nextNodeData: this.destNodes,
      nextNodeReadFlag: this.nodeReadOnly,
      nodePlaceholder: this.nodePlaceholder,
      assignInfos: this.assignInfos
    })
  }

  // 
  clearAssignInfos() {
    this.assignInfos = [];
    this.vm.open({
      assignInfos: this.assignInfos
    })
  }

  /**
   * 
   */
  back() {
    this.clearProcess();
    this.subject.next({ type: 'success', message: '' });
  }

  submit() {
    if (!this.selectedProcessId) {
      this.subject.next({ type: 'error', message: '' });
      return;
    }
    if (!this.selectedNodeId) {
      this.subject.next({ type: 'error', message: '' });
      return;
    }
    if (this.assignInfos.filter(info => ((info.assigneeInfo && info.assigneeInfo.assigneeUsers.items.length) || info.selectedAssignees.length) && !info.assignees.length).length) {
      this.subject.next({ type: 'error', message: '' });
      return;
    }
    const nodes: string[] = this.selectedNodeId.split('');
    for (let i = 0; i < nodes.length; i++) {
      let nextFlowNode: FlowNodeInfo = {
        activityDefinitionId: nodes[i],
        activityDefinitionName: this.assignInfos[i].activityName,
        dependency: this.assignInfos[i].dependency,
        selectedAssignees: this.assignInfos[i].assigneeInfo ? this.assignInfos[i].assignees : [],
        assigneeInfo: null,
        resolved: false,
        anonymous: false,
        anonymousText: ''
      }
      this.payload.nextFlowNodes.push(nextFlowNode);
    }
    this.payload.processDefinitionId = this.selectedProcessId;
    this.taskService.startProcess(this.payload).subscribe(() => {
      this.clearProcess();
      this.subject.next({ type: 'success', message: '' });
    }, (error) => {
      this.subject.next({ type: 'error', message: error && error.response && error.response.data && error.response.data.Message || '' });
    })
  }

  getSubject() {
    return this.subject.asObservable();
  }

  clearProcess() {
    this.vm.close();
    this.vm = null;
    this.selectedProcessId = ''
    this.selectedNodeId = ''
    this.processReadOnly = false;
    this.nodeReadOnly= true;
  }
}
