import { ViewModelContext, StateMachine } from '@farris/mobile-devkit';
import {  EMPTY } from 'rxjs';
/**
 * 
 */
class StateMachineService {

    constructor(private viewModelContext: ViewModelContext) {
    }

    /**
     * 
     */
    executeAction(action: string) {
      if (!action) {
        return EMPTY;
      }

      if (typeof this.viewModelContext.stateMachine[action] !== 'function') {
        return EMPTY;
      }
      
      this.viewModelContext.stateMachine[action]();
    }
}

export { StateMachineService };
