import { Observable } from 'rxjs';
import { HttpClient } from '@farris/mobile-devkit';
import { CancelSubmitPayload } from '../entity/start-process-payload';



export class TaskService {

    constructor(
        private httpSvc: HttpClient,
    ) { }

    error(error: any, actionCode?: string) {
        if (error && error.error && error.error.Message) {
            var message = error.error.Message;
            if (actionCode) {
                message = actionCode + ":" + message;
            }
            return message;
        }
    }

    /**
     * 
     * @param payload API
     */
    startProcessSimulate(payload: any): Observable<any> {
        const url = `/api/runtime/wf/v1.0/processInstances/startProcess-simulation`;
        return this.httpSvc.post(url, payload, {});
    }

    /**
     * 
     * @param payload API
     */
    startProcess(payload: any): Observable<any> {
        const url = `/api/runtime/wf/v1.0/processInstances/startProcess`;
        return this.httpSvc.post(url, payload, {});
    }

    /**
     * 
     * @param param 
     */
    cancelSubmit(param: CancelSubmitPayload): Observable<any> {
        const url = `/api/runtime/wf/v1.0/processInstances/cancelSubmit`;
        return this.httpSvc.post(url, param, {});
    }

}
