import { Observable, Subject, of } from 'rxjs';
import { TaskService } from './service/task.service';
import { StartProcessPayload, CancelSubmitPayload } from './entity/start-process-payload';
import { ProcessSelectorService } from './submit/process-selector-vue/process-selector.service'

import { LoadingService, NotifyService } from '../ui-services/index';
interface Response { type: string, message: string };

export class WfTaskHandlerService {

  subject = new Subject<any>();

  constructor(
    private processSelectorService: ProcessSelectorService,
    private taskService: TaskService,
    private loadingService: LoadingService,
    private notifyService: NotifyService
  ) { }

  /**
   * 
   * @param payload 
   */
  startProcess(payload: StartProcessPayload): Observable<any> {
    const subject = new Subject();
    this.processSelectorService.setParam(payload);
    this.processSelectorService.getSubject().subscribe(
      (response: Response) => {
        if (response.type === 'success') {
          if (response.message) {
            this.notifyService.success(response.message);
            subject.next(true)
          }
        } else if (response.type === 'error') {
          subject.next(false)
          this.notifyService.error(response.message);
        }
      },
      () => {
        this.loadingService.hide();
      })
    return subject.asObservable();
  }

  /**
   * 
   * @payload payload 
   */
  cancelSubmit(payload: CancelSubmitPayload): Observable<any> {
    const subject = new Subject();
    this.loadingService.show();
    this.taskService.cancelSubmit(payload).subscribe(re => {
      subject.next(true);
      this.loadingService.hide();
      this.notifyService.success('');
    }, error => {
      subject.next(false);
      this.loadingService.hide();
      this.notifyService.error(error && error.response && error.response.data && error.response.data.Message || '');
    })
    return subject.asObservable();
  }

  /**
 * 
 * @param dataId id
 * @param bizDefKey id
 */
  submitWithBizDefKey(dataId: string, bizDefKey: string): Observable<any> {
    if (!dataId) {
      this.notifyService.error('id');
      return;
    }
    if (!bizDefKey) {
      this.notifyService.error('Id');
      return;
    }
    var startProcessPayload = new StartProcessPayload();
    startProcessPayload.dataId = dataId;
    startProcessPayload.bizDefKey = bizDefKey;
    return this.startProcess(startProcessPayload);
  }

  /**
   * 
   * @param dataId id
   * @param bizDefKey id
   */
  cancelSubmitWithDataId(dataId: string, bizDefKey: string): Observable<any> {
    if (!dataId) {
      this.notifyService.error('id');
      return;
    }
    if (!bizDefKey) {
      this.notifyService.error('Id');
      return;
    }
    var cancelSubmitPayload = new CancelSubmitPayload();
    cancelSubmitPayload.dataId = dataId;
    cancelSubmitPayload.bizDefKey = bizDefKey;
    return this.cancelSubmit(cancelSubmitPayload);
  }

  /**
   * 
   * @param dataIds ID
   * @param bizDefKey id
   */
  batchSubmitWithBizDefKey(dataIds: string[], bizDefKey: string): Observable<any> {
    if (!dataIds) {
      this.notifyService.error('dataId');
      return;
    }
    if (!dataIds.length) {
      this.notifyService.error('');
      return;
    }
    if (!bizDefKey) {
      this.notifyService.error('ID');
      return;
    }
    if (dataIds.length === 1) {
      var startPayload = new StartProcessPayload()
      startPayload.bizDefKey = bizDefKey;
      startPayload.dataId = dataIds[0];
      return this.startProcess(startPayload);
    }

    var successNum: number = 0;
    var failedNum: number = 0;
    const subject = new Subject();
    this.loadingService.show();
    for (var i = 0; i < dataIds.length; i++) {
      var startPayload = new StartProcessPayload()
      startPayload.bizDefKey = bizDefKey;
      startPayload.dataId = dataIds[i];
      startPayload.variables = { simulate: false };
      this.taskService.startProcess(startPayload).subscribe((response: any) => {
        if (response.procDefs.length > 1 || response.nextFlowNodes.length > 1) { ////
          failedNum++;
        } else {
          successNum++;
        }
        if (successNum + failedNum === dataIds.length) {
          this.loadingService.hide();
          subject.next(true);//
          this.showBatchSubmitInfo("submit", successNum, failedNum);
        }
      }, () => {
        failedNum++;
        if (successNum + failedNum === dataIds.length) {
          this.loadingService.hide();
          subject.next(false);//
          this.showBatchSubmitInfo("submit", successNum, failedNum);
        }
      })
    }
    return subject.asObservable();
  }


  /**
   * 
   * @param dataIds ID
   * @param bizDefKey id
   */
  batchCancelSubmitWithDataId(dataIds: string[], bizDefKey: string): Observable<any> {
    if (!dataIds) {
      this.notifyService.error('dataId');
      return;
    }
    if (!dataIds.length) {
      this.notifyService.error('');
      return;
    }
    if (!bizDefKey) {
      this.notifyService.error('ID');
      return;
    }
    const subject = new Subject();
    var successNum: number = 0;
    var failedNum: number = 0;
    this.loadingService.show();

    for (var i = 0; i < dataIds.length; i++) {
      var cancelSubmitPayload = new CancelSubmitPayload();
      cancelSubmitPayload.dataId = dataIds[i];
      cancelSubmitPayload.bizDefKey = bizDefKey;
      this.taskService.cancelSubmit(cancelSubmitPayload).subscribe(re => {
        successNum++;
        if (successNum + failedNum === dataIds.length) {
          this.loadingService.hide();
          subject.next(true);
          this.showBatchSubmitInfo("cancel", successNum, failedNum);
        }
      }, () => {
        failedNum++;
        if (successNum + failedNum === dataIds.length) {
          this.loadingService.hide();
          subject.next(false);
          this.showBatchSubmitInfo("cancel", successNum, failedNum);
        }
      })
    }
    return subject.asObservable();
  }

  /**
 * 
 * @param parentDataId id
 * @param childDataId id
 * @param bizDefKey id
 */
  childSubmit(parentDataId: string, childDataId: string, bizDefKey: string): Observable<any> {
    if (!parentDataId || !childDataId) {
      this.notifyService.error('dataId');
      return;
    }
    if (!bizDefKey) {
      this.notifyService.error('ID');
      return;
    }
    var startProcessPayload = new StartProcessPayload();
    startProcessPayload.dataId = `${parentDataId},${childDataId}`;
    startProcessPayload.bizDefKey = bizDefKey;
    return this.startProcess(startProcessPayload);
  }

  /**
 * 
 * @param parentDataId id
 * @param childDataId id
 * @param bizDefKey id
 */
  childCancelSubmit(parentDataId: string, childDataId: string, bizDefKey: string): Observable<any> {
    if (!parentDataId || !childDataId) {
      this.notifyService.error('dataId');
      return;
    }
    if (!bizDefKey) {
      this.notifyService.error('ID');
      return;
    }
    var cancelSubmitPayload = new CancelSubmitPayload();
    cancelSubmitPayload.dataId = `${parentDataId},${childDataId}`;
    cancelSubmitPayload.bizDefKey = bizDefKey;
    return this.cancelSubmit(cancelSubmitPayload);
  }

  /**
 * 
 * @param parentDataId id
 * @param childDataIds id
 * @param bizDefKey id
 */
  childBatchSubmit(parentDataId: string, childDataIds: string[], bizDefKey: string): Observable<any> {
    if (!parentDataId || !childDataIds) {
      this.notifyService.error('dataId');
      return;
    }
    if (!bizDefKey) {
      this.notifyService.error('ID');
      return;
    }
    const dataIds = childDataIds.map(childDataId => `${parentDataId},${childDataId}`);
    return this.batchSubmitWithBizDefKey(dataIds, bizDefKey);
  }

  /**
 * 
 * @param parentDataId id
 * @param childDataIds id
 * @param bizDefKey id
 */
  childBatchCancelSubmit(parentDataId: string, childDataIds: string[], bizDefKey: string): Observable<any> {
    if (!parentDataId || !childDataIds) {
      this.notifyService.error('dataId');
      return;
    }
    if (!bizDefKey) {
      this.notifyService.error('ID');
      return;
    }
    const dataIds = childDataIds.map(childDataId => `${parentDataId},${childDataId}`);
    return this.batchCancelSubmitWithDataId(dataIds, bizDefKey);
  }

  /**
 * 
 */
  private showBatchSubmitInfo(actionCode: string, successNum: number, failedNum: number) {
    if (successNum && !failedNum) {//
      actionCode === "submit" && this.notifyService.success('');
      actionCode === "cancel" && this.notifyService.success('');
    }
    if (successNum && failedNum) {//
      this.notifyService.warning(`${successNum}${failedNum}!`);
    }
    if (!successNum && failedNum) {//
      actionCode === "submit" && this.notifyService.error('');
      actionCode === "cancel" && this.notifyService.error('');
    }
  }

}
