import { ChangeDetail, ChangeDetailType } from '@farris/mobile-bef';
import { BusinessUploadFileInfo, BusinessAttachmentInfo } from './types';


/**
 * 
 */
class AttachmentUtil {

  /**
   * 
   */
  public static convertToAttachmentInfos(fileInfos: BusinessUploadFileInfo[]): BusinessAttachmentInfo[] {
    if (!fileInfos) {
      return [];
    }
    const attachmentInfos: BusinessAttachmentInfo[] = fileInfos.map((fileInfo: BusinessUploadFileInfo) => {
      return this.convertToAttachmentInfo(fileInfo);
    });
    return attachmentInfos;
  }

  /**
   * 
   */
  public static convertToAttachmentInfo(fileInfo: BusinessUploadFileInfo): BusinessAttachmentInfo {
    const attachmentInfo: BusinessAttachmentInfo = {
      attachmentId: fileInfo.metadataId,
      fileName: fileInfo.fileName
    };
    return attachmentInfo;
  }

  /**
   * 
   */
  public static getFirstAttachmentInfo(attachmentInfos: BusinessAttachmentInfo[]) {
    if (!attachmentInfos || attachmentInfos.length === 0) {
      return;
    }
    const firstBusinessAttachmentInfo = attachmentInfos[0];
    return firstBusinessAttachmentInfo;
  }

  /**
   * id
   */
  public static peekAttachmentIds(attachmentInfos: BusinessAttachmentInfo[]) {
    if (!attachmentInfos) {
      attachmentInfos = [];
    }
    const attachmentIds = attachmentInfos.map((attachmentInfo: BusinessAttachmentInfo) => {
      return attachmentInfo.attachmentId;
    });

    return attachmentIds;
  }

  /**
   * 
   */
  public static getChangeDetails(oldListData, newListData): ChangeDetail[] {
    const changeDetails = [];
    newListData.forEach((newData: any) => {
      let oldData = oldListData.find((oldData: any) => {
        return oldData['id'] === newData['id'];
      });

      if (!oldData) {
        oldData = {};
      }

      const changeDetail = this.getChangeDetail(oldData, newData);
      if (changeDetail) {
        changeDetails.push(changeDetail);
      }
    });

    return changeDetails;
  }

  /**
   * 
   */
  public static getChangeDetail(oldData, newData): ChangeDetail {
    const changeDetail: ChangeDetail = {
      ChangeType: ChangeDetailType.Modify,
      ChangeInfo: {
        DataId: newData['id']
      }
    };
    let isChanged = false;

    Object.keys(newData).forEach((propName) => {

      // 
      if (propName === 'id' || propName === 'nodeCode' || propName === 'childs') {
        return;
      }
      
      const oldValue = oldData[propName];
      const newValue = newData[propName];

      if (oldValue === undefined && typeof newValue === 'object') {
        return;
      }

      if (JSON.stringify(oldValue) !== JSON.stringify(newValue)) {
        changeDetail.ChangeInfo[propName] = newValue;
        isChanged = true;
      }
    });

    return isChanged ? changeDetail : null;

  }

}

export { AttachmentUtil };
